import React, { useContext } from 'react';
import Calendar from 'Components/calendar';
import Footer from 'Components/footer';
import { ThemeContext } from 'Context/ThemeContext';
import styled from 'styled-components';
import { IThemeProps } from 'Types/theme';
import { Light, TextColor } from 'Assets/styles/colors';

const Item = styled.li<IThemeProps>`
  color: ${(props) => (props.isLight ? TextColor : Light)};
`;

const Description = styled.span<IThemeProps>`
  color: ${(props) => (props.isLight ? TextColor : Light)};
`;

const Library = (): JSX.Element => {
  const { isLight } = useContext(ThemeContext);

  return (
    <div>
      <Description isLight={isLight}>
        Стэк технологий, с которым я работаю:{' '}
      </Description>
      <ul>
        <Item isLight={isLight}>Javascript / Typescript</Item>
        <Item isLight={isLight}>React</Item>
        <Item isLight={isLight}>Vue</Item>
        <Item isLight={isLight}>React-native</Item>
        <Item isLight={isLight}>Redux</Item>
        <Item isLight={isLight}>HTML5\CSS\SCSS</Item>
        <Item isLight={isLight}>Webpack\Figma\Photoshop</Item>
      </ul>
      <Calendar isLight={isLight} />
      <Footer isLight={isLight} />
    </div>
  );
};

export default Library;
